html,
body {
    height: 100%;
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
}

body {
    overflow: hidden;
}

.logtext {
    height: 95%;
    width: auto;
    overflow-x: hidden;
    overflow-y: auto;
    border: 1px solid #c0c0c0;
    padding: 5px;
    p {
        width: 100%;
        word-wrap: break-word;
    }
    .data-item{
        display: none;
        &.data-trace-item{
            .show-trace&{
                display: block;
            }
        }
        &.data-function-item{
            .show-function&{
                display: block;
            }
        }
    }
}

.failResult {
    color: red;
    font-weight: bold;
}

.passResult {
    color: green;
    font-weight: bold;
}

.valLable {
    color: black;
    font-weight: bold;
    margin-right: 2px;
}

.valdata {
    color: black;
    font-weight: normal;
}

.botBorder {
    width: 100%;
    height: 5px;
    border-bottom: 1px dashed #000000;
    margin-bottom: 10px;
}

h1 {
    display: block;
    font-weight: bold;
    font-size: 22px;
}

h2 {
    display: block;
    font-weight: bold;
    font-size: 20px;
}

h3 {
    display: block;
    font-weight: bold;
    font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 10px;
    margin-bottom: 10px;
}

.filters {
    position: fixed;
    top: 0;
    right: 20px;
    background-color: #333;
    color: #fff;
    padding: 5px;
}

.filter {
    position: relative;
    padding-left: 15px;

    &:before {
        content: '';
        width: 10px;
        height: 10px;
        border-radius: 5px;
        background-color: #f00;
        position: absolute;
        left: 0;
        top: 3px;
    }
    &.active{
        &:before {
            background-color: #0f0;
        }
    }
}